<script>
  import { Layer } from 'svelte-canvas';

  export let x = 0,
    y = 0,
    w = 1,
			h=1,
    fill = 'black',
    stroke = null,
    strokeWidth = 1;


  $: render = ({ context }) => {
    context.fillStyle = fill;
		context.beginPath();
		context.rect(x, y, w, h);
		context.fill();

  };
</script>

<Layer {render} />