<script>
  import { onMount } from 'svelte';
import { Canvas } from 'svelte-canvas';
import { extent } from 'd3-array';
import { scaleLinear,scaleLog } from 'd3-scale';
import Axis from './Axis.svelte';
import Rects from './Rects.svelte';
import Point from './Point.svelte';
import { timer, elapsed } from "./clock.js";  
let run = true;
let showAverage=false
let showNiche
let N=200
let y=Array(N).fill(0.01)
let ym=Array(N).fill(0.01)
let x=Array.from(new Array(N), (x, i) => i/N*40.0)
let Y
let r=0.1
let E=20.0
let dy=Array(N).fill(0.0)
let lag=Array(N*10).fill(0.0)
let v=Array(N*10).fill(0.0)
let lagx=Array.from(new Array(N), (x, i) => i)
let w=425.0
let K=50
let m=0.05
let dt=1.0
let im=0.0001
let imexp=1
let maxdy
let s=0
let av=0.0
let steps=1
let t=0.0
let tE=0.0
let espeed=1.0
let ma=1.0
let mae=0.0

let points=[{mpg:10, hp:3, id:1},{mpg:20, hp:5, id:3},{mpg:15, hp:8, id:2},{mpg:20, hp:5, id:4}]
  let picked, click = false;
let width, height;
  const margin = { top: 10, right: 10, bottom: 25, left: 25 };
const average = arr => arr.reduce( ( p, c ) => p + c, 0 ) / arr.length;

function mean(arr,x) {return arr.reduce( ( p, c,i ) => p + c*x[i], 0 ) / arr.reduce( ( p, c ) => p + c, 0 )};
function variance(arr,x) {return arr.reduce( ( p, c,i ) => p + Math.pow(2,av-x[i])*c, 0 ) / arr.reduce( ( p, c ) => p + c, 0 )};

const onReset = () => {
  y.forEach((e,i)=>{y[i]=0.1/N})
  timer.reset();
};

  $: xs = scaleLinear()
  .domain(extent(x))
  .range([margin.left, width - margin.right])
  .nice();

$: ys = scaleLinear()
  .domain([0.001,K/15])//[0.001,K/25]) //extent(y)
  .range([height - margin.bottom, margin.top])
  //.nice();

   $: xl = scaleLinear()
  .domain(extent(lag))
  .range([margin.left, width - margin.right])
  .nice();

$: yl = scaleLinear()
  .domain([2000,0]) //extent(y)
  .range([height - margin.bottom, margin.top])
  .nice();

  $: zs = scaleLinear()
  .domain([-0.005,0,0.005])
  .range(["steelblue", "darkorange", "forestgreen"])
  .nice();
$: im=Math.exp(imexp-15)
$: ma=Math.exp(-mae)

$: run ? timer.start() : timer.stop();

$: {$elapsed;//ensures vis updates only occur as often as screenupdates
    s+=1
    for (let i=0;i<steps;i++){
    t+=espeed*dt
     Y=y.reduce((a, b) => a + b, 0)
    E+=20+Math.sin(t/1500)*10-E
    y.forEach((e,i)=>{
      //dy[i]=y[i]*(r*(1-Y/K)*Math.exp(-Math.pow(E-x[i],2)/w)-m)+im;
      dy[i]=y[i]*(r*(1-Y/K)*0.59*Math.exp(0.0633*E)*(1-(Math.pow(E-x[i],2)/w))-m);
      y[i]=Math.max(0.0,y[i]+dt*dy[i])+dt*im
      ym[i]+=ma*(y[i]-ym[i])});
      
      }
      av=mean(y,x)
    lag[s%2000]=(E-av) //Math.abs
    lagx[s%2000]=s
    v[s%2000]=0.01*variance(y,x)
    
    //console.log(v)
   }
/*
    <Rects
      x={xs(x[i])}
      y={0}
      w={1}
      h={ys(y[i])}
      fill={zs(dy[i])}
    />
*/

</script>
<div class=layout>
<div class="setting">

    <button on:click="{() => (run = !run)}">{run ? "pause" : "resume"}			</button>
    <button on:click="{onReset}">reset</button>
  <span>Total biomass</span>
    <progress id="file" max="100" value={Y/K*100}/> 


    <span>steps</span><input id="npf" type="range" max=300 min =1 step=0.01 bind:value={steps}>
    <span>base input</span><input id="base input" type="range" max=8 min =1 step=0.01 bind:value={imexp}>
    <span>E rate</span><input id="espeed" type="range" max=5.0 min =1.0 step=0.1 bind:value={espeed}>
    <span>Time average</span><input id="fsdg" type="range" max=20.0 min =0.0 step=0.1 bind:value={mae}>
 

</div>
<div class="chart-container" bind:clientWidth={width} bind:clientHeight={height}>
<Canvas
  {width}
  {height}
>

  {#each y as yy,i}
  {#if showNiche}
  {#if Math.max(0.0,(r*0.59*Math.exp(0.0633*E)*(1-(Math.pow(E-x[i],2)/w)))-m)>0}
    <Point
      x={xs(x[i])}
      y={ys(0)}
      fill={"lightgreen"}
      r={3}
    />
  {/if}
  {#if Math.max(0.0,(r*(1-Y/K)*0.59*Math.exp(0.0633*E)*(1-(Math.pow(E-x[i],2)/w)))-m)>0}
      <Point
      x={xs(x[i])}
      y={ys(0)}
      fill="forestgreen"
      r={3}
    />
        {/if}
  {/if}
{#if showAverage}
  <Point
      x={xs(x[i])}
      y={ys(ym[i])}
      fill="black"
      r={1}
    />
 {:else}
    <Rects
      x={xs(x[i])}
      y={ys(0.00001)}
      w={width/(N*1.1)}
      h={ys(ym[i])-ys(0.00001)}
      fill={zs(dy[i])}
    />
    {/if}
  {/each}

  <Point
      x={xs(E)}
      y={250}
      fill="crimson"
      r={5}
    />
      <Point
      x={xs(av)}
      y={250}
      fill="forestgreen"
      r={5}
    />

      <Axis type="x" scale={xs} tickNumber={8} {margin} />
  
</Canvas>
</div>
</div>

<style>
div {
  display: flex;
  justify-content: left;
  margin-bottom: 3em;
}
button {
  margin: 0.5em;
  padding: 0.5em;
  border: none;
  background: #eee;
  font-size: 14px;
  font-weight: bold;
  border-radius: 4px;
  cursor: pointer;
}
  .chart-container {
  height: 90%;
  width:90%
}

.layout{
  display:grid;
  grid-template-columns: 20% 80%;
  width:100%;
  height:100%
}
.setting{
  display:flex;
  flex-direction: column;
}
</style>


